<template>
  <div>
    <Container
      title=''
      sub-title=' checkbox '
    >
      <ki-check-box label='' v-model='checked' />
    </Container>
    <Container
      title=''
      sub-title=''
    >
      <ki-check-box label='' v-model='checked1' disabled />
      <ki-check-box label='1' v-model='checked2' disabled />
    </Container>
    <Container
      title=''
      sub-title=''
    >
      <ki-check-box-group v-model='hobby1'>
        <ki-check-box label=''></ki-check-box>
        <ki-check-box label=''></ki-check-box>
        <ki-check-box label='Rap'>Rap</ki-check-box>
      </ki-check-box-group>
      <div style='margin-top: 20px; font-size: 14px; color: var(--color)'>
        {{hobby1.join(', ')}}
      </div>
    </Container>
    <Container
      title='indeterminate '
      sub-title='indeterminate  checkbox '
    >
      <ki-check-box
        label=''
        v-model='allChecked'
        @change='handleAllChecked'
        :indeterminate='hobby.length > 0 && hobby.length !== 3'
      />
      <ki-check-box-group v-model='hobby' style='margin-top: 10px;' @change='handleHobbyChange'>
        <ki-check-box label='1'></ki-check-box>
        <ki-check-box label='2'></ki-check-box>
        <ki-check-box label='3'>Rap</ki-check-box>
      </ki-check-box-group>
    </Container>
    <Attributes :attributes='attributes' title='Checkbox Attributes' />
    <Attributes :attributes='checkboxEvents' target='event' title='Checkbox Events' />
    <Attributes :attributes='groupAttributes' title='CheckboxGroup Attributes' />
    <Attributes :attributes='checkboxGroupEvents' target='event' title='CheckboxGroup Events' />
  </div>
</template>

<script lang='ts'>
import { defineComponent, ref } from 'vue';
import { CheckBox } from '@/components';
import { Container, Attributes } from './component';

export default defineComponent({
  name: 'CheckboxExample',
  components: {
    Container,
    Attributes,
    KiCheckBox: CheckBox,
    KiCheckBoxGroup: CheckBox.Group,
  },
  setup() {
    const checked = ref(true);
    const checked1 = ref(true);
    const checked2 = ref(false);
    const allChecked = ref(false);
    const hobby = ref(['1']);
    const hobby1 = ref(['']);

    const handleAllChecked = (isChecked: boolean) => {
      if (isChecked) {
        hobby.value = ['1', '2', '3'];
      } else {
        hobby.value = [];
      }
    };
    const handleHobbyChange = (value: (string | number)[]) => {
      allChecked.value = value.length === 3;
    };

    return {
      checked,
      checked1,
      checked2,
      allChecked,
      hobby,
      hobby1,
      handleAllChecked,
      handleHobbyChange,
      attributes: [
        {
          attribute: 'v-model',
          description: 'checkbox ',
          type: 'string | number | boolean',
          option: '',
          default: '',
        },
        {
          attribute: 'label',
          description: ' group  group ',
          type: 'string | number',
          option: '',
          default: '',
        },
        {
          attribute: 'disabled',
          description: '',
          type: 'boolean',
          option: 'true | false',
          default: 'false',
        },
        {
          attribute: 'indeterminate',
          description: ' indeterminate ',
          type: 'boolean',
          option: 'true | false',
          default: 'false',
        },
      ],
      checkboxEvents: [
        {
          name: 'change',
          description: '',
          type: '(value: boolean) => void',
        },
      ],
      groupAttributes: [
        {
          attribute: 'v-model',
          description: 'checkbox-group ',
          type: '(string | number)[]',
          option: '',
          default: '',
        },
      ],
      checkboxGroupEvents: [
        {
          name: 'change',
          description: '',
          type: '(value: (string | number)[]) => void',
        },
      ],
    };
  },
});
</script>

