<template>
 <div>
   <Container title=''>
     <a @click='handleOpen'> Message Box</a>
   </Container>
   <Container title=''>
     <a @click='handleOpen2'> Message Box</a>
   </Container>
   <Container
     title=''
     sub-title=''
   >
     <a @click='handleOpen3'> Message Box</a>
   </Container>
   <Container
     title=''
     sub-title=''
   >
     <a @click='handleOpen4'> Message Box</a>
   </Container>
   <Container
     title='HTML '
     sub-title='message  HTML '
   >
     <a @click='handleOpen5'> Message Box</a>
   </Container>
   <Container
     title=''
     sub-title=''
   >
     <a @click='handleOpen6'> Message Box</a>
   </Container>
   <Container
     title=''
     sub-title=' beforeClose  message box callback ,  loading '
   >
     <a @click='handleOpen7'> Message Box</a>
   </Container>
 </div>
</template>

<script lang='ts'>
import { defineComponent } from 'vue';
import { MessageBox, Message } from '@/components';
import { Container } from './component';

export default defineComponent({
  name: 'MessageBoxExample',
  components: {
    Container,
  },
  setup() {
    const { alert: $alert, confirm: $confirm, prompt: $prompt } = MessageBox;
    const handleOpen = () => {
      $alert('..', {
        confirmButtonText: '',
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen2 = () => {
      $confirm(', ?', '', {
        iconType: 'warning',
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen3 = () => {
      $prompt('', {
        inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        inputErrorMessage: '',
        inputPlaceholder: '',
      }).then((value) => {
        Message.success(`: ${value}`);
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen4 = () => {
      $confirm(', ?', '', {
        iconType: 'warning',
        center: true,
        mask: false,
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen5 = () => {
      $alert('<i> <span style="color: orange;">HTML</span> </i>', 'HTML ', {
        dangerouslyUseHTMLString: true,
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen6 = () => {
      $confirm('', '', {
        iconType: 'warning',
        distinguishCancelAndClose: true,
      }).then(() => {
        Message.success({
          content: '',
        });
      }).catch((action) => {
        Message.info(action === 'cancel' ? '' : '');
      });
    };

    const handleOpen7 = () => {
      $confirm(', ?', '', {
        iconType: 'warning',
        beforeClose(done, action) {
          if (action === 'confirm') {
            setTimeout(done, 2000);
          } else {
            done();
          }
        },
      }).then(() => {
        Message.success('');
      }).catch(() => {
        Message.warning('');
      });
    };

    return {
      handleOpen,
      handleOpen2,
      handleOpen3,
      handleOpen4,
      handleOpen5,
      handleOpen6,
      handleOpen7,
    };
  },
});
</script>

<style lang='less' scoped>
  a {
    color: var(--primary-shallow-color);
    font-size: 14px;
    cursor: pointer;

    &:hover {
      color: var(--primary-color);
    }
  }
</style>
