
import { defineComponent } from 'vue';
import { MessageBox, Message } from '@/components';
import { Container } from './component';

export default defineComponent({
  name: 'MessageBoxExample',
  components: {
    Container,
  },
  setup() {
    const { alert: $alert, confirm: $confirm, prompt: $prompt } = MessageBox;
    const handleOpen = () => {
      $alert('..', {
        confirmButtonText: '',
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen2 = () => {
      $confirm(', ?', '', {
        iconType: 'warning',
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen3 = () => {
      $prompt('', {
        inputPattern: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        inputErrorMessage: '',
        inputPlaceholder: '',
      }).then((value) => {
        Message.success(`: ${value}`);
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen4 = () => {
      $confirm(', ?', '', {
        iconType: 'warning',
        center: true,
        mask: false,
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen5 = () => {
      $alert('<i> <span style="color: orange;">HTML</span> </i>', 'HTML ', {
        dangerouslyUseHTMLString: true,
      }).then(() => {
        Message.success('ok');
      }).catch(() => {
        Message.warning('cancel');
      });
    };

    const handleOpen6 = () => {
      $confirm('', '', {
        iconType: 'warning',
        distinguishCancelAndClose: true,
      }).then(() => {
        Message.success({
          content: '',
        });
      }).catch((action) => {
        Message.info(action === 'cancel' ? '' : '');
      });
    };

    const handleOpen7 = () => {
      $confirm(', ?', '', {
        iconType: 'warning',
        beforeClose(done, action) {
          if (action === 'confirm') {
            setTimeout(done, 2000);
          } else {
            done();
          }
        },
      }).then(() => {
        Message.success('');
      }).catch(() => {
        Message.warning('');
      });
    };

    return {
      handleOpen,
      handleOpen2,
      handleOpen3,
      handleOpen4,
      handleOpen5,
      handleOpen6,
      handleOpen7,
    };
  },
});
